
import { defineComponent } from 'vue';

interface Task {
  id: number;
  task: string;
  done: boolean;
}

export default defineComponent({
  data() {
  return {
    listTasks: [
      {id: 1, task: "Organizar uma videoconferÃªncia de equipe para discutir o progresso do projeto e definir metas para a prÃ³xima semana.", done: true},
      {id: 2, task: "Realizar uma revisÃ£o de carreira, atualizando o currÃ­culo e estabelecendo metas de desenvolvimento profissional", done: false},
      {id: 3, task: "Fazer um estudo aprofundado sobre um tÃ³pico de interesse acadÃªmico.", done: false}
    ],
    newTask: ''
  }
 },
 methods: {
     getTask() {
        const tasknew = {id: this.listTasks.length + 1, task: this.newTask, done: false};
          if(tasknew.task){
            this.listTasks.push(tasknew)
            this.newTask = '';
          }
          
     },

     deleteTask(taskToDelete: { id: number; task: string }) {
      const taskIdToDelete = taskToDelete.id;
      const taskIndex = this.listTasks.findIndex(task => task.id === taskIdToDelete);
      if (taskIndex !== -1) {
        this.listTasks.splice(taskIndex, 1);
      }
    },

    updateTaskCompletion(task: Task) {
      let taskDone = task.done
      taskDone = !task.done;

      this.countCompletedTasks();
    },

    countCompletedTasks(){
      const completedTasks = this.listTasks.filter(task => task.done === true);
      return completedTasks.length;
    }
 },

});

